import React, { useMemo, useState } from 'react';
import { createIntl, createIntlCache, RawIntlProvider } from 'react-intl';
import { messages as allMessages } from 'lang';
import type { SupportedLocale } from 'lang';

const cache = createIntlCache();

const getLocaleFromUrl = (): SupportedLocale => {
  const params = new URLSearchParams(window.location.search);
  const lang = params.get('lang');
  return lang === 'en' ? 'en' : 'ko';
};

const HCIntlProvider: React.FC<React.PropsWithChildren> = ({ children }) => {
  const [locale] = useState<SupportedLocale>(getLocaleFromUrl());

  const intl = useMemo(
    () =>
      createIntl(
        {
          locale,
          messages: allMessages[locale],
        },
        cache,
      ),
    [locale],
  );

  return <RawIntlProvider value={intl}>{children}</RawIntlProvider>;
};

export default HCIntlProvider;
