import React from 'react';
import { HelmetProvider } from 'react-helmet-async';

import HCIntlProvider from './HCIntlProvider';
import HCThemeProvider from './HCThemeProvider';

interface HCProviderProps {}

const HCProvider: React.FunctionComponent<React.PropsWithChildren<HCProviderProps>> = ({ children }) => {
  return (
    <HCIntlProvider>
      <HelmetProvider>
        <HCThemeProvider>{children}</HCThemeProvider>
      </HelmetProvider>
    </HCIntlProvider>
  );
};

export default HCProvider;
