import React, { useMemo } from 'react';
import { ThemeProvider } from '@mui/material';
import { lightTheme } from 'app/theme';

interface HCThemeProviderProps {}

const HCThemeProvider: React.FunctionComponent<React.PropsWithChildren<HCThemeProviderProps>> = ({ children }) => {
  const theme = useMemo(() => {
    const getUserTheme = () => {
      return lightTheme;
    };

    return getUserTheme();
  }, []);

  return <ThemeProvider theme={theme}>{children}</ThemeProvider>;
};

export default HCThemeProvider;
