import React from 'react';
import { createTheme, ThemeOptions } from '@mui/material';

declare module '@mui/material/styles' {
  interface TypographyVariants {
    error: React.CSSProperties;
    ellipsis: React.CSSProperties;
  }

  // allow configuration using `createTheme`
  interface TypographyVariantsOptions {
    error?: React.CSSProperties;
    ellipsis?: React.CSSProperties;
  }
}

declare module '@mui/material/Typography' {
  interface TypographyPropsVariantOverrides {
    error: true;
    ellipsis: true;
  }
}
// by deankim : createTheme을 하지 않으면 기본으로 지정된 값이 h1, h2, body 같은 속성에 적용되지 않음.
const defaultTypography: ThemeOptions = createTheme({
  typography: {
    fontFamily: ['Pretendard', 'sans-serif'].join(','),
    fontWeightRegular: 400,
    fontWeightMedium: 500,
    fontWeightBold: 700,
    h1: {
      fontWeight: 700,
      fontSize: '2rem',
      lineHeight: 1.3,
    },
    h2: {
      fontWeight: 600,
      fontSize: '1.5rem',
      lineHeight: 1.4,
    },
    h3: {
      fontWeight: 600,
      fontSize: '1.25rem',
      lineHeight: 1.4,
    },
    h4: {
      fontWeight: 600,
      fontSize: '1.125rem',
      lineHeight: 1.45,
    },
    h5: {
      fontWeight: 600,
      fontSize: '1rem',
      lineHeight: 1.4,
    },
    body1: {
      fontWeight: 500,
      fontSize: '0.9375rem',
      lineHeight: 1.45,
    },
    body2: {
      fontWeight: 500,
      fontSize: '0.875rem',
      lineHeight: 1.45,
    },
    subtitle1: {
      fontWeight: 500,
      fontSize: '0.8125rem',
      lineHeight: 1.5,
    },
    subtitle2: {
      fontWeight: 500,
      fontSize: '0.75rem',
      lineHeight: 1.5,
    },
    error: {
      color: '#ff0202',
    },
    caption: {
      fontWeight: 400,
      fontSize: '0.6875rem',
      lineHeight: 1.5,
      color: '#6C6E78',
    },
    ellipsis: {
      whiteSpace: 'nowrap',
      overflow: 'hidden',
      textOverflow: 'ellipsis',
    },
  },
});

export default { typography: defaultTypography.typography };
