import { createTheme, Palette } from '@mui/material/styles';
import { deepmerge } from '@mui/utils';
import { defaultComponent } from './components';
import { darkPalette, lightPalette } from './palette';
import { defaultTypography } from './typography';
import etc from './etc';

const darkDefaultTheme = createTheme({ palette: { mode: 'dark' } }, darkPalette);
const lightDefaultTheme = createTheme({ palette: { mode: 'light' } }, lightPalette);

const typographyTheme = deepmerge(defaultTypography, etc);

const darkPaletteTheme = deepmerge(darkDefaultTheme, typographyTheme);
const lightPaletteTheme = deepmerge(lightDefaultTheme, typographyTheme);

const darkTheme = deepmerge(darkPaletteTheme, defaultComponent(darkPalette.palette as Palette));
const lightTheme = deepmerge(lightPaletteTheme, defaultComponent(lightPalette.palette as Palette));

export { darkTheme, lightTheme };
