import React from 'react';
import './App.css';
import { CssBaseline } from '@mui/material';
import HCProvider from 'app/provider';
import { Outlet } from 'react-router-dom';

function App(): React.ReactElement {
  return (
    <HCProvider>
      <CssBaseline />
      <div className="App">
        <Outlet />
      </div>
    </HCProvider>
  );
}
export default App;
