import React, { lazy } from 'react';
import Loadable from 'components/Loadable';
import App from 'App';

const RootPage = Loadable(lazy(() => import('view/root')));
const AIPage = Loadable(lazy(() => import('view/ai')));
const ExamplePage = Loadable(lazy(() => import('view/example')));

const Routes = [
  {
    path: '',
    element: <App />,
    children: [
      {
        path: '/',
        element: <RootPage />,
      },
      {
        path: '/ai',
        element: <AIPage />,
      },
      {
        path: '/ex',
        element: <ExamplePage />,
      },
    ],
  },
];

export default Routes;
