import { Palette, ThemeOptions } from '@mui/material';

const DefaultComponents = (palette: Palette): ThemeOptions => {
  return {
    components: {
      MuiCssBaseline: {
        styleOverrides: {
          ':root': {
            '--min-width': '360px',
            '--radius-small': '12px',
            '--radius-medium': '20px',
            '--radius-large': '30px',
            '--icon-small': '20px',
            '--icon-medium': '24px',
            '--icon-large': '42px',
            '--account-menu-width': '300px',
            '--notification-height': '100vh - 40px - 16px',
            '--min-notification-height': '244px',
            '--max-notification-height': '500px',
            '--toolbar-height': '72px',
            '--elevation-01': '0 2px 10px 0 rgba(32, 33, 39, 0.15)',
            '--elevation-02': '0px 2px 10px 0px rgba(0, 0, 0, 0.12)',
            '--max-content-view-width': '1280px',
            '--gutter-between-banner': '8px',
            '--gutter-regular': '40px',
            '--gutter-small': '20px',
            '--search-popover-verticalsize': '162px',
            '--search-popover-verticalsize-sm-down': '150px',
            '--icon-primary-color': palette.UIColor.iconColor,
            '--icon-secondary-color': palette.UIColor.secondaryIconColor,
            '--icon-tertiary-color': palette.UIColor.tertiaryIconColor,
            '--icon-quaternary-color': palette.UIColor.quaternaryIconColor,
            '--side-sheet-width': '360px',
            '--side-sheet-header-height': '56px',
          },
        },
      },
      MuiAppBar: {
        styleOverrides: {
          root: {
            backgroundColor: palette.background.default,
          },
        },
      },
      MuiIcon: {
        styleOverrides: {
          root: {
            width: '24px',
            height: '24px',
          },
          fontSizeLarge: {
            width: '48px',
            height: '48px',
          },
          fontSizeSmall: {},
        },
      },
      MuiButton: {
        styleOverrides: {
          root: {
            minWidth: '36px',
            minHeight: '36px',
            padding: '0 14px',
            color: palette.text.primary,
            borderRadius: '0',
            textTransform: 'none',
            '&:hover': {
              backgroundColor: palette.HCColor.buttonHover,
            },
            '.MuiTypography-root': {
              whiteSpace: 'nowrap',
              color: palette.text.primary,
            },
          },
          startIcon: {
            marginRight: '6px',
            marginLeft: '-6px',
          },
        },
        variants: [
          {
            props: { variant: 'contained' },
            style: {
              padding: '0 16px',
              borderRadius: '4px',
              background: palette.HCColor.btnBg,
              boxShadow: '0 3px 3px 0 rgba(0, 0, 0, 0.15)',
              '&:hover': {
                boxShadow: '0 3px 3px 0 rgba(0, 0, 0, 0.15)',
              },
              'svg path:last-child': {
                fill: palette.HCColor.svgIconSecondary,
              },
              '.MuiButton-startIcon': {
                marginRight: '4px',
                marginLeft: '-8px',
              },
            },
          },
          {
            props: { variant: 'contained', color: 'brand' },
            style: {
              minHeight: '42px',
              height: '42px',
              padding: '0 20px',
              fontSize: '0.875rem',
              fontWeight: '600',
              borderRadius: 'var(--radius-small)',
              boxShadow: 'none',
              textTransform: 'none',
              '--variant-containedBg': palette.brand.main,
              '--variant-color': '#FFFFFF',
              backgroundColor: 'var(--variant-containedBg)',
              color: 'var(--variant-color)',
              '&:hover': {
                '--variant-containedBg': palette.brand.dark,
                backgroundColor: 'var(--variant-containedBg)',
                boxShadow: 'none',
              },
            },
          },
          {
            props: { variant: 'contained', color: 'accent' },
            style: {
              minHeight: '42px',
              height: '42px',
              padding: '0 20px',
              fontSize: '0.875rem',
              fontWeight: '600',
              borderRadius: 'var(--radius-small)',
              boxShadow: 'none',
              textTransform: 'none',
              '--variant-containedBg': palette.accent.main,
              '--variant-color': '#FFFFFF',
              backgroundColor: 'var(--variant-containedBg)',
              color: 'var(--variant-color)',
              '&:hover': {
                '--variant-containedBg': palette.UIColor.hoverAccentColor,
                backgroundColor: 'var(--variant-containedBg)',
                boxShadow: 'none',
              },
            },
          },
          {
            props: { variant: 'outlined' },
            style: {
              borderColor: palette.UIColor.borderColor,
              '&:hover': {
                borderColor: palette.UIColor.borderColor,
                backgroundColor: palette.UIColor.hoverBackgroundColor,
              },
            },
          },
          {
            props: { variant: 'outlinedNew' },
            style: {
              minHeight: '42px',
              height: '42px',
              padding: '0 20px',
              fontSize: '0.875rem',
              fontWeight: '600',
              border: '1px solid',
              borderRadius: 'var(--radius-small)',
              borderColor: palette.UIColor.borderColor,
              '&:hover': {
                borderColor: palette.UIColor.borderColor,
                backgroundColor: palette.UIColor.hoverBackgroundColor,
              },
              '.MuiTypography-body2': {
                color: palette.UIColor.labelColor,
                fontWeight: '600',
              },
            },
          },
          {
            props: { variant: 'text' },
            style: {
              padding: '0 14px',
              borderRadius: '0',
              '.MuiButton-startIcon': {
                marginRight: '4px',
              },
              '&.MuiButton-textSecondary': {
                minWidth: '40px',
                fontWeight: '700',
                color: palette.secondary.main,
              },
            },
          },
          {
            props: { variant: 'textOnly' },
            style: {
              minWidth: 'auto',
              padding: '0',
              '&:hover': {
                backgroundColor: 'transparent',
                textDecoration: 'underline',
              },
            },
          },
          {
            props: { variant: 'textOnly', color: 'accent' },
            style: {
              minWidth: 'auto',
              minHeight: '42px',
              height: '42px',
              padding: '0',
              fontSize: '0.875rem',
              fontWeight: '600',
              '--variant-color': palette.accent.main,
              color: 'var(--variant-color)',
              '&:hover': {
                textDecoration: 'none',
              },
              '.MuiTypography-root': {
                color: 'inherit',
              },
            },
          },
          {
            props: { variant: 'textCombined' },
            style: {
              padding: '0',
              borderRadius: 'var(--radius-small)',
              '.MuiButton-endIcon': {
                marginLeft: '0',
                marginRight: '0',
              },
            },
          },
        ],
      },
      MuiToggleButton: {
        styleOverrides: {
          root: {
            textTransform: 'none',
            fontSize: '0.875rem',
            fontWeight: 'normal',
            letterSpacing: 'initial',
          },
        },
      },
      MuiIconButton: {
        styleOverrides: {
          root: {
            color: 'inherit',
            borderRadius: '0',
            '&:hover': {
              background: palette.UIColor.onHoverBackgroundColor,
            },
          },
          sizeLarge: {
            width: 'var(--icon-large)',
            height: 'var(--icon-large)',
            padding: '8px',
            borderRadius: 'var(--icon-large)',
          },
        },
      },
      MuiDrawer: {
        styleOverrides: {
          paper: {
            width: 300,
            borderRight: 'none',
            color: palette.text.primary,
            backgroundColor: palette.background.default,
          },
        },
      },
      MuiOutlinedInput: {
        styleOverrides: {
          root: {
            minWidth: ' 150px',
            fontSize: '0.75rem',
            background: palette.HCColor.dialogBg,

            '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
              borderWidth: '1px',
            },

            '.MuiOutlinedInput-notchedOutline': {
              borderRadius: '4px',
              borderWidth: '1px',
              borderColor: palette.HCColor.softBorder,
            },
          },
        },
      },
      MuiPaper: {
        styleOverrides: {
          rounded: {
            borderRadius: '4px',
          },
        },
      },
      MuiPopover: {
        styleOverrides: {
          paper: {
            boxShadow: '0px 10px 10px 0px rgba(0,0,0,0.1)',
          },
        },
      },
      MuiAlert: {
        styleOverrides: {
          root: {
            fontSize: '0.75rem',
            lineHeight: '1.5',
            color: 'white',
          },
          message: {
            padding: 0,
          },
        },
      },
      MuiTooltip: {
        styleOverrides: {
          popper: {
            '&[data-popper-reference-hidden]': {
              display: 'none',
            },
            '&[data-popper-escaped]': {
              display: 'none',
            },
          },
        },
        defaultProps: {
          PopperProps: {
            modifiers: [
              {
                name: 'hideWhenReferenceHidden',
                enabled: true,
                phase: 'main',
                fn({ state, instance }) {
                  if (state.modifiersData.hide?.isReferenceHidden) {
                    instance.destroy();
                  }
                },
              },
            ],
          },
        },
      },
      MuiCheckbox: {
        styleOverrides: {
          root: {
            padding: '2px',
          },
        },
      },
      MuiChip: {
        variants: [
          {
            props: { variant: 'chipButton' },
            style: {
              height: '42px',
              padding: '9px 8px',
              fontSize: '0.875rem',
              lineHeight: '1.45',
              fontWeight: 600,
              borderRadius: 'var(--radius-large)',
              '.MuiChip-label': {
                paddingLeft: '8px',
                paddingRight: '8px',
              },
              '.MuiChip-icon': {
                marginLeft: '4px',
                marginRight: '-4px',
              },
            },
          },
          {
            props: { variant: 'chipButton', color: 'default' },
            style: ({ theme }) => ({
              backgroundColor: theme.palette.UIColor.selectedBackgroundColor,
              '&:hover': {
                backgroundColor: theme.palette.UIColor.selectedHoverBackgroundColor,
              },
            }),
          },
          {
            props: { variant: 'chipButton', color: 'transparent' },
            style: ({ theme }) => ({
              backgroundColor: 'transparent',
              '&:hover': {
                backgroundColor: theme.palette.UIColor.hoverBackgroundColor,
              },
            }),
          },
          {
            props: { variant: 'outlined' },
            style: {
              height: '42px',
              padding: '9px 12px',
              borderRadius: 'var(--radius-large)',
              borderColor: palette.UIColor.borderColor,
              transition: 'none',
              '.MuiChip-label': {
                paddingLeft: '0px',
                paddingRight: '0px',
                color: palette.UIColor.subLabelColor,
                '.MuiTypography-root': {
                  fontWeight: '600',
                },
              },
              '.MuiChip-icon': {
                marginLeft: '0',
                marginRight: '4px',
                color: 'var(--icon-secondary-color)',
              },
              '.MuiChip-deleteIcon': {
                marginLeft: '2px',
                marginRight: '-4px',
                color: palette.UIColor.tertiaryIconColor,
              },
            },
          },
        ],
      },
      MuiTypography: {
        variants: [
          {
            props: { variant: 'body2-subLabel' },
            style: {
              fontSize: '0.875rem',
              lineHeight: 1.45,
              color: palette.UIColor.subLabelColor,
              fontWeight: '400',
            },
          },
        ],
      },
      MuiSkeleton: {
        styleOverrides: {
          root: ({ theme }) => ({
            backgroundColor: theme.palette.UIColor.secondaryBackgroundColor,
          }),
        },
        variants: [
          {
            props: { variant: 'text' },
            style: {
              borderRadius: 'var(--radius-small)',
              height: '34px',
            },
          },
        ],
      },
    },
  };
};

declare module '@mui/material/Button/Button' {
  interface ButtonPropsColorOverrides {
    brand: true;
    accent: true;
  }
  interface ButtonPropsVariantOverrides {
    textOnly: true;
    textCombined: true;
    outlinedNew: true;
  }
}

declare module '@mui/material/Chip/Chip' {
  interface ChipPropsColorOverrides {
    transparent: true;
  }
  interface ChipPropsVariantOverrides {
    chipButton: true;
  }
}

declare module '@mui/material/Typography' {
  interface TypographyPropsVariantOverrides {
    'body2-subLabel': true;
  }
}

export default DefaultComponents;
